/* =============================================
   SIDEBAR CUSTOM STYLES
   ============================================= */

// Enhanced Sidebar Styles
.main-sidebar {
  // Smooth transitions for sidebar collapse/expand - menggunakan CSS native
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

// Enhanced Sidebar Collapse Styles
.sidebar-menu {
  > li {
    // Improved active state with better visual indicators
    &.active {
      > a {
        background: linear-gradient(90deg, rgba(59, 130, 246, 0.15) 0%, transparent 100%);
        box-shadow: inset 0 0 0 1px rgba(59, 130, 246, 0.1);
        
        // Add a subtle glow effect for active items
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          width: 3px;
          background: #3c8dbc;
          border-radius: 0 2px 2px 0;
        }
      }
    }

    // Enhanced treeview collapse/expand indicators
    &.treeview {
      > a {
        position: relative;
        
        > .fa-angle-left,
        > .pull-right-container > .fa-angle-left {
          transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
          transform-origin: center;
          margin-right: 8px;
        }
      }

      // When menu is open
      &.menu-open {
        > a {
          background-color: rgba(255, 255, 255, 0.08);
          
          > .fa-angle-left,
          > .pull-right-container > .fa-angle-left {
            transform: rotate(-90deg);
            color: #3c8dbc;
          }
        }

        // Enhanced treeview menu appearance
        > .treeview-menu {
          background: linear-gradient(180deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.05) 100%);
          border-left: 2px solid rgba(60, 141, 188, 0.3);
          margin-left: 8px;
          border-radius: 0 0 4px 4px;
        }
      }
    }
  }

  // Enhanced treeview menu items
  .treeview-menu {
    > li {
      > a {
        transition: all 0.2s ease;
        position: relative;
        
        // Dot indicator - hidden by default, only show on hover
        &::before {
          content: '';
          position: absolute;
          left: 20px;
          top: 50%;
          transform: translateY(-50%);
          width: 4px;
          height: 4px;
          background-color: #8aa4af;
          border-radius: 50%;
          transition: all 0.2s ease;
          opacity: 0; // Hidden by default
        }

        &:hover {
          padding-left: 45px;
          background-color: rgba(255, 255, 255, 0.08);
          
          &::before {
            opacity: 1; // Show dot on hover
            background-color: #3c8dbc;
            transform: translateY(-50%) scale(1.2);
          }
        }
      }

      // Active state for treeview items
      &.active > a {
        background-color: rgba(60, 141, 188, 0.15);
        color: #fff;
        padding-left: 35px;
        
        // No dot for active state by default
        &::before {
          opacity: 0;
        }
        
        // Show dot only when active item is hovered
        &:hover {
          padding-left: 45px;
          
          &::before {
            opacity: 1;
            background-color: #3c8dbc;
            transform: translateY(-50%) scale(1.2);
          }
        }
      }
    }
  }
}

// Improved sidebar headers
.sidebar-menu {
  li.header {
    background: linear-gradient(90deg, #1a2226 0%, #2c3b41 100%);
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    font-weight: 600;
    letter-spacing: 0.5px;
    padding: 12px 25px 12px 15px;
    position: relative;
    
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 15px;
      right: 15px;
      height: 1px;
      background: linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.1) 50%, transparent 100%);
    }
  }
}

// Enhanced badge styles
.sidebar-menu {
  .badge {
    font-size: 9px;
    font-weight: 600;
    padding: 4px 8px;
    border-radius: 10px;
    min-width: 20px;
    text-align: center;
    
    // Different badge colors based on context
    &.badge-success {
      background-color: #00a65a;
    }
    
    &.badge-warning {
      background-color: #f39c12;
    }
    
    &.badge-danger {
      background-color: #dd4b39;
    }
    
    &.badge-info {
      background-color: #00c0ef;
    }
  }
}

// Improved user panel
.user-panel {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.02);
  
  > .info {
    > p {
      font-size: 13px;
      font-weight: 600;
    }
    
    > a {
      font-size: 11px;
      opacity: 0.8;
      transition: all 0.2s ease;
      
      &:hover {
        opacity: 1;
        transform: translateX(2px);
      }
    }
  }
}

// Animation for menu items
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-10px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.sidebar-menu > li {
  animation: slideIn 0.3s ease forwards;
  
  // Stagger animation for child items
  &:nth-child(1) { animation-delay: 0.05s; }
  &:nth-child(2) { animation-delay: 0.1s; }
  &:nth-child(3) { animation-delay: 0.15s; }
  &:nth-child(4) { animation-delay: 0.2s; }
  &:nth-child(5) { animation-delay: 0.25s; }
}

// Responsive improvements
@media (max-width: 767px) {
  .main-sidebar {
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.3);
  }
}